@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;600&display=swap);
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  background: #00000051;
}

:root {
  --header-color: #00ffd9;
}

#header_title {
  display: flex;
  justify-content: center;
  font: 6em 'Poppins', sans-serif;
  font-weight: 600;
  color: #00ffd9;
  color: var(--header-color);
}

#header_horizontal_rule {
  height: 0.125em;
  background: #00ffd9;
  background: var(--header-color);
  border: none;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#general_info_form {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(2, 1fr);
  margin: 2em;
}

.general_info_input {
  width: 15em;
  height: 1.5em;
  border: 1px solid black;
  border-radius: 0.25em;
}

.general_info_label {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font: 1.1em 'Poppins', sans-serif;
  color: black;
  font-weight: 300;
  margin: 0.5em;
  width: 21em;
}

.add_education_form {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin: 1em;
}

.add_education_label {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  font: 1.1em 'Poppins', sans-serif;
  width: 14em;
  margin: 0 1em;
}

.add_education_input {
  height: 1.5em;
  border: 1px solid black;
  border-radius: 0.25em;
}

.add_experience_form {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin: 1em;
}

.add_experience_label {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  font: 1.1em 'Poppins', sans-serif;
  width: 14em;
  margin: 0 1em;
}

.add_experience_input {
  height: 1.5em;
  border: 1px solid black;
  border-radius: 0.25em;
}

